/* General Styles */
.get_hero {
  width: 100%;
  height: 365px;
  background-color: rgb(36, 27, 69);
  padding-left: 9%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-right: 20vh;
  overflow: hidden;
}
.get_hero_content {
  height: 100%;
  width: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.get_hero_content h1 {
  width: 529px;
  font-size: 62px;
  font-weight: 700;
  line-height: 74px;
  color: white;
  text-align: center;
}
.get_hero_content p {
  margin-top: 20px;
  width: 560px;
  color: white;
  font-weight: 500;
  font-size: 20px;
  line-height: 26px;
  text-align: center;
}
.contact {
  width: 100%;
  height: 704px;
  padding-left: 9%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-right: 20vh;
  overflow: hidden;
}
.contact_content {
  width: 980px;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 30px;
}
.contact_content > h1 {
  width: 100%;
  font-size: 38px;
  line-height: 46px;
  font-weight: 700;
  color: rgb(15, 15, 42);
}
.contact_content > p {
  margin-top: 20px;
  width: 100%;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  color: rgb(15, 15, 42);
}
.contact_form {
  padding-top: 50px;
  width: 100%;
  height: 470px;
  display: flex;
  gap: 40px;
  overflow-x: auto; 
}
.contact_form_left,
.contact_form_right {
  width: 450px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.labls {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.labls > label {
  font-size: 16px;
  line-height: 8px;
  font-weight: 400;
}
input,
select {
  width: 100%;
  height: 48px;
  padding-left: 30px;
  outline: none;
  box-shadow: inset black 0px 0px 20px -12px;
  border-radius: 5px;
}
input[type="text"],
input[type="number"],
input[type="email"] {
  font-size: 16px;
  padding-left: 30px;
}
/* Add this to your GetInTouch.css file */
.select-margin {
  padding-left: 30px;
}

.checkbox {
  display: flex;
  gap: 15px;
}
#checkbox {
  box-shadow: none;
  width: 40px;
  height: 40px;
}
.contanctbtn {
  font-size: 20px;
  letter-spacing: 5px;
  line-height: 20px;
  font-weight: 700;
  width: 120px;
  padding-bottom: 10px;
  text-transform: capitalize;
  border-bottom: solid #74cdaf;
}

/* Responsive Styles */
@media screen and (max-width: 1024px) {
  .get_hero_content h1, .get_hero_content  {
    width: 80%;
    font-size: 50px;
  }
}

@media screen and (max-width: 768px) {
  .get_hero{
    padding: 0;
  }
  .get_hero_content h1  {
    width: 80%;
    font-size: 50px;
  }
  .contact {
    padding-top: 30px;
    padding-right: 5%;
    padding-left: 5%;
  }
  .contact_content {
    margin-top: 30px;
    width: 100%;
    margin-bottom: 30px;
  }
  .contact_form {
    margin-top: 20px;
    flex-direction: column;
    height: auto;
  }
  .contact_form_left,
  .contact_form_right {
    width: 100%;
  }
}

@media screen and (max-width: 480px) {
  .get_hero_content h1 {
    width: 100%;
    font-size: 36px;
    line-height: 44px;
  }
  .get_hero_content p {
    width: 100%;
    font-size: 16px;
  }
  .contact_content > h1 {
    font-size: 28px;
    line-height: 34px;
  }
  .contact_content > p {
    font-size: 14px;
    line-height: 20px;
  }
  

/* CSS */
.button-4 {
  appearance: none;
  width: 100px;
  margin-bottom: 60px;
  background-color: #FAFBFC;
  border: 1px solid rgba(27, 31, 35, 0.15);
  border-radius: 6px;
  box-shadow: rgba(27, 31, 35, 0.04) 0 1px 0, rgba(255, 255, 255, 0.25) 0 1px 0 inset;
  box-sizing: border-box;
  color: #24292E;
  cursor: pointer;
  display: inline-block;
  font-family: -apple-system, system-ui, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji";
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  list-style: none;
  padding: 6px 16px;
  position: relative;
  transition: background-color 0.2s cubic-bezier(0.3, 0, 0.5, 1);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: middle;
  white-space: nowrap;
  word-wrap: break-word;
}

.button-4:hover {
  background-color: #F3F4F6;
  text-decoration: none;
  transition-duration: 0.1s;
}

.button-4:disabled {
  background-color: #FAFBFC;
  border-color: rgba(27, 31, 35, 0.15);
  color: #959DA5;
  cursor: default;
}

.button-4:active {
  background-color: #EDEFF2;
  box-shadow: rgba(225, 228, 232, 0.2) 0 1px 0 inset;
  transition: none 0s;
}

.button-4:focus {
  outline: 1px transparent;
}

.button-4:before {
  display: none;
}

.button-4:-webkit-details-marker {
  display: none;
}
}
